.foot {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row-reverse;
  background-color: #444649;
  width: 100%;
  position: relative;
  bottom: 0;
  color: white;
  justify-content: space-between;
  font-size: 16px;
  padding: 1rem 1.5rem 1rem;
}

.footer-title {
  font-size: 16px;
  font-weight: 600;
}

.contato {
  float: right;
  margin: 0 15px 0 0;
  /* font-size: 14px; */

}

.property {
  display: flex;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  flex-direction: column;
  font-size: 12px;
  letter-spacing: 0.5px;
  margin: 0;
  line-height: 15px;
  margin: 0;
  font-weight: 600;
}

.arrow-top {
  /* margin: 10px 0 10px 15px; */
  cursor: pointer;
}

.arrow-top--draw {
  margin: 0 0 5px 40px;
  width: 20px;
  height: 20px;
  border-right: 4px solid white;
  border-bottom: 4px solid white;
  transform: rotate(225deg);
  z-index: 1;
}

.arrow-top--text {
  font-size: 15px;
}

.arrow-top--text:hover {
  cursor: pointer;

}

.property--rights {
  margin-top: 40px;
  justify-self: flex-end;
}

@media only screen and (max-width: 780px) {
  .foot {
    display: flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 8px;
  }

}

@media only screen and (max-width: 620px) {
  .foot {
    display: flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 5px;
  }

  .contato {
    float: right;
    margin-right: 50px;
  }

  .address {
    font-size: 14px;
  }

  .property--rights {
    font-size: 12px;
  }

}